.modal {
    overflow-y: initial !important;
}

.modal-body {
    height: 55vh;
    overflow-y: auto;
}

.portrait {
    width: 50%;
    border-radius: 10px;
    box-shadow: 0 5px 5px grey;
}

.video {
    box-shadow: 0 5px 5px grey;
    border-radius: 10px;
}

@media (min-width: 992px) {
    .modal-body {
        height: 70vh;
    }
}
